import React, { Component } from "react";
import styled from "styled-components";
import { rem } from "polished";
import anime from "animejs";

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 102px;
  .point {
    font-size: 30px;
    margin-bottom: 40px;
    padding: 12px 0;
    position: relative;
    .line {
      opacity: 0;
      position: absolute;
      left: 0;
      height: 1px;
      width: 100%;
      background-color: #fff;
      transform-origin: 0 0;
      &.line1 {
        top: 0;
      }
      &.line2 {
        bottom: 0;
      }
    }
  }
  .words {
    display: flex;
    .word .letter {
      transform-origin: 50% 100%;
      display: inline-block;
    }
    .left {
      display: flex;
      flex-direction: column;
      margin-right: 22px;
      > .line {
        display: flex;
        align-items: center;
        justify-content: flex-end;
      }
      .low {
        font-family: "Fashion Zhonghei";
        font-size: 32px;
        font-weight: 500;
        padding-right: 21px;
        &.boom {
          font-family: "Fashion Zhonghei";
          padding-right: 12px;
        }
      }
      .line1 {
        margin-bottom: 8px;
        padding-right: 10px;
        text-align: left;
        justify-content: flex-start;
        .leaflet {
          font-size: 26px;
          padding-right: 19px;
        }
      }
      .line2 {
        padding-bottom: 30px;
        text-align: right;
        .ball {
          font-size: 15px;
          padding-right: 14px;
        }
      }
      .line3 {
        font-size: 20px;
        text-align: right;

        .ai {
          padding-right: 10px;
        }
        .zs {
          color: #ee3a44;
          padding: 7px 15px;
          background: #fff;
        }
      }
    }
    .right {
      .what {
        font-size: 20px;
        writing-mode: vertical-lr;
      }
    }
  }
  .solution {
    display: flex;
    flex-direction: column;
    align-items: center;
    .title {
      font-family: "Fashion Zhonghei";
      font-size: 30px;
    }
    .sub_title {
      margin-top: 23px;
      font-size: 20px;
    }
  }
`;
export default class Zhaosheng extends React.PureComponent {
  constructor() {
    super();
    this.wrapper = React.createRef();
  }
  componentDidMount() {
    this.wrapper.current
      .querySelectorAll(".point .letters,.words .word")
      .forEach(node => {
        console.log("node", node);

        let arr = node.innerText;
        let newArr = arr.split("").map(t => `<span class='letter'>${t}</span>`);
        node.innerHTML = newArr.join("");
      });
    const wordBox = this.wrapper.current.querySelector(".words");
    const solutionBox = this.wrapper.current.querySelector(".solution");
    const titleLetters = this.wrapper.current.querySelectorAll(
      ".point .letter"
    );
    const titleLines = this.wrapper.current.querySelectorAll(".point .line");
    const firstLetters = this.wrapper.current.querySelectorAll(
      ".words .word.first .letter"
    );
    const secondLetters = this.wrapper.current.querySelectorAll(
      ".words .word.second .letter"
    );
    const thirdLetters = this.wrapper.current.querySelectorAll(
      ".words .word.third .letter"
    );
    const fourthLetters = this.wrapper.current.querySelectorAll(
      ".words .word.fourth .letter"
    );
    const zsLetter = this.wrapper.current.querySelector(".words .word.zs");
    console.log("thirdLetters", thirdLetters);

    anime
      .timeline({ loop: true })
      .add({
        targets: titleLetters,
        scale: [0.3, 1],
        opacity: [0, 1],
        translateZ: 0,
        easing: "easeOutExpo",
        duration: 600,
        delay: function(el, i) {
          return 70 * (i + 1);
        }
      })
      .add({
        targets: titleLines,
        scaleX: [0, 1],
        opacity: [0.5, 1],
        easing: "easeOutExpo",
        duration: 700,
        offset: "-=875",
        delay: function(el, i, l) {
          return 80 * (l - i);
        }
      })
      .add({
        targets: firstLetters,
        scale: [0, 1],
        duration: 1500,
        elasticity: 600,
        delay: function(el, i) {
          return 45 * (i + 1);
        }
      })
      .add({
        targets: secondLetters,
        scale: [0, 1],
        duration: 1500,
        elasticity: 600,
        delay: function(el, i) {
          return 45 * (i + 1);
        }
      })
      .add({
        targets: thirdLetters,
        scale: [0, 1],
        duration: 1500,
        elasticity: 600,
        delay: function(el, i) {
          return 80 * (i + 1);
        }
      })
      .add({
        targets: fourthLetters,
        scale: [0, 1],
        duration: 1500,
        elasticity: 900,
        delay: function(el, i) {
          return 45 * (i + 1);
        }
      })
      .add({
        targets: zsLetter,
        scale: [5, 1],
        opacity: [0, 1],
        duration: 2000,
        elasticity: 600
      })
      .add({
        targets: wordBox,
        scale: [1, 0],
        opacity: [1, 0],
        duration: 2000,
        elasticity: 600,
        complete: () => {
          wordBox.remove();
        }
      })
      .add({
        targets: solutionBox,
        scale: [8, 1],
        opacity: [0, 1],
        duration: 1000,
        elasticity: 600
      });
  }
  render() {
    return (
      <Content ref={this.wrapper}>
        <h1 className="point">
          <span className="line line1" />
          <span className="line line2" />
          <span className="letters"></span>
        </h1>
        <div className="words">
          <div className="left">
            <p className="line line1">
              <span className="word first leaflet"></span>
              <span className="word second low">LOW</span>
            </p>
            <p className="line line2">
              <span className="word first ball"></span>
              <span className="word second low boom"></span>
            </p>
            <p className="line line3">
              <span className="word fourth auto"></span>
              <span className="word fourth ai"></span>
              <span className="word zs"></span>
            </p>
          </div>
          <div className="right">
            <span className="word third what"></span>
          </div>
        </div>
        <div className="solution">
          <p className="title"></p>
          <p className="sub_title"></p>
        </div>
      </Content>
    );
  }
}
