import React, { Component } from "react";
import styled, { keyframes } from "styled-components";
import MusicImg from "../assets/img/music.png";
// import BgMusic from "../assets/bg.mp3";
const rotation = keyframes`
  from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
`;
const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  width: 35px;
  height: 35px;
  position: fixed;
  top: 20px;
  right: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  border: 1px solid #fff;
  audio {
    display: none;
  }
  img {
    width: 23px;
    &.playing {
      animation: ${rotation} 2s infinite linear;
    }
  }
`;
export default class Music extends Component {
  state = {
    playing: false
  };
  constructor() {
    super();
    this.bgMusic = React.createRef();
  }
  onTogglePlay = () => {
    const { playing } = this.state;
    const bgM = this.bgMusic.current;
    if (playing) {
      bgM.pause();
    } else {
      bgM.play();
    }
    this.setState({
      playing: !playing
    });
  };
  render() {
    const { playing } = this.state;
    return (
      <Wrapper onClick={this.onTogglePlay}>
        <img
          src={MusicImg}
          alt=""
          className={playing ? `playing` : null}
        />
        <audio
          autoPlay={true}
          ref={this.bgMusic}
          src="http://www.170hi.com/kw/other.web.nu01.sycdn.kuwo.cn/resource/n1/34/26/611434910.mp3"
        />
      </Wrapper>
    );
  }
}
