import React, { Component } from "react";
import styled from "styled-components";
import { rem } from "polished";
import ani from "animejs";

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgba(255, 255, 255, 1);
  padding-top: 115px;
  .lines {
    width: 90%;
    display: flex;
    flex-direction: column;
    .line {
      font-size: 14px;
      margin-bottom: 28px;
      align-self: center;
      &.left {
        align-self: flex-start;
      }
      &.right {
        align-self: flex-end;
      }
      em {
        margin-left: 17px;
        font-size: 24px;
        font-weight: 800;
      }
    }
  }
  .how {
    font-size: 30px;
    margin-bottom: 70px;
    font-weight: 800;
  }
  .answer {
    font-family: "Fashion Zhonghei";
    font-size: 24px;
    margin-bottom: 33px;
    overflow: hidden;
    .letter {
      display: inline-block;
    }
  }
  .tip {
    font-size: 14px;
    overflow: hidden;
    .letter {
      display: inline-block;
    }
  }
`;
export default class Intro extends Component {
  constructor() {
    super();
    this.wrapper = React.createRef();
  }
  componentDidMount() {
    const wrapperEle = this.wrapper.current;
    wrapperEle.querySelectorAll(".tip,.answer").forEach(node => {
      let arr = node.innerText;
      let newArr = arr.split("").map(t => `<span class='letter'>${t}</span>`);
      node.innerHTML = newArr.join("");
    });
    const lines = wrapperEle.querySelectorAll(".lines .line");
    const how = wrapperEle.querySelector(".how");
    const letters = wrapperEle.querySelectorAll(".answer .letter,.tip .letter");
    ani
      .timeline()
      .add({
        targets: lines,
        delay: (el, i) => {
          return i * 500;
        },
        translateX: [-600, 0]
      })
      .add({
        targets: how,
        opacity: [0, 1],
        scale: [8, 1.5]
      })
      .add({
        targets: letters,
        translateY: ["1.1em", 0],
        translateZ: 0,
        duration: 750,
        delay: function(el, i) {
          return 50 * i;
        }
      });
  }
  render() {
    return (
      <Content ref={this.wrapper}>
        <div className="lines">
          <p className="line left">
            <em></em>
          </p>
          <p className="line">
            <em></em>
          </p>
          <p className="line right">
            <em></em>
          </p>
        </div>
        <p className="how"></p>
        <p className="answer"></p>
        <p className="tip"></p>
      </Content>
    );
  }
}
